.main-footer {
  color: rgba($dark-gray, 0.5);
  margin-top: auto;
}

.main-footer p {
  margin: 0;
}

.main-footer__links {
  border-bottom: 1px solid $border-footer;
}

.main-footer__links-wrapper,
.main-footer__legal-data,
.main-footer__copyright,
.main-footer__nav-list,
.main-footer__main {
  display: flex;
  flex-wrap: wrap;
}

.main-footer__links-wrapper {
  margin: 0 auto;
  padding: 3.39vw 5.05vw 3.073vw 4.48vw;
  justify-content: space-between;
  max-width: 83.3vw;
}

.main-footer .main-footer__wrapper-social img {
  width: 11.2vw;
  height: 2.71vw;
  margin-left: 0.36vw;
}

.main-footer__text-hidden {
  display: none;
}

.main-footer__social {
  padding-top: 2.5vw;
}

.main-footer__main {
  width: 74.5%;
  justify-content: inherit;
}

.main-footer h3 {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 0.78vw;
  line-height: 0.83vw;
  color: $dark-gray;
  letter-spacing: 0.75px;
  text-transform: uppercase;
  padding-left: 0.10vw;
  padding-bottom: 1.51vw;
}

.main-footer__menu {
  margin-left: 3.44vw;
}

.main-footer__navigation {
  display: flex;
  flex-wrap: wrap;
}

.main-footer__nav-list {
  max-width: 26.875vw;
  max-height: 5.2vw;
  flex-direction: column;
}

.main-footer__element {
  margin-right: 3.9vw;
}

.main-footer__item {
  font-size: 0.78vw;
  line-height: 1.3vw;
  color: inherit;
  transition-duration: 0.3s;
  transition-property: color;
  outline: none;

  &:hover,
  &:focus {
    color: $blue-color;
  }

  &:active {
    color: darken($blue-color, 10%);
  }
}

.main-footer .main-footer__office p {
  font-size: 0.78vw;
  line-height: 0.83vw;
}

.main-footer__tel-list {
  padding-top: 0.47vw;
}

.main-footer__legal-data {
  padding: 0.73vw 5.05vw 0.73vw 4.74vw;
  font-size: 0.68vw;
  line-height: 1.25vw;
  text-transform: uppercase;
  letter-spacing: 0.52px;
}

.main-footer .main-footer__legal-data p {
  margin-left: auto;
}

.main-footer__copyright {
  width: 23.8vw;
  max-width: 23.8vw;
  justify-content: space-between;
}

.main-footer .main-footer__copyright p {
  margin-left: 0;
}

.main-footer__copyright + .main-footer__link {
  margin-left: 7.4vw;
  margin-right: auto;
}

.main-footer__link {
  color: inherit;
  transition-duration: 0.3s;
  transition-property: color;

  &:hover,
  &:focus {
    color: $blue-color;
  }

  &:active {
    color: darken($blue-color, 10%);
  }
}

@media (max-width: $tablet-width) {
  .main-footer__links-wrapper {
    padding: 0;
    flex-direction: column;
    max-width: 100%;
  }

  .main-footer__header {
    border-bottom: 1px solid $border-footer;
  }

  .main-footer__wrapper-social {
    display: flex;
    padding: 2.5vw 2.08vw 3.26vw 2.7vw;
    justify-content: space-between;
    max-width: 100vw;
    flex-wrap: wrap;
  }
  
  .main-footer .main-footer__wrapper-social img {
    width: 28.1vw;
    height: 6.8vw;
  }

  .main-footer__text-hidden {
    display: block;
    font-size: 1.7vw;
    line-height: 3.1vw;
    text-transform: uppercase;
    letter-spacing: 0.52px;
    align-self: center;
    padding-top: 0.65vw;
    padding-left: 2.21vw;
  }

  .main-footer__social {
    padding-top: 1.5vw;
  }

  .main-footer__main {
    width: 100%;
    padding: 5.1vw 2.08vw 4.55vw 3.3vw;
    justify-content: start;
  }

  .main-footer__menu {
    width: 50%;
    margin-left: 0;
  }

  .main-footer h3 {
    font-size: 1.95vw;
    line-height: 2.08vw;
    font-weight: 400;
    padding-bottom: 3.4vw;
  }

  .main-footer__nav-list {
    width: 100%;
    max-height: 100%;
    max-width: 100%;
  }

  .main-footer__element {
    margin-right: 0;
  }

  .main-footer__item,
  .main-footer .main-footer__office p {
    font-size: 1.95vw;
    line-height: 3.26vw;
  }

  .main-footer__office {
    padding-left: 1.56vw;
  }

  .main-footer__tel-list {
    padding-top: 0;
  }

  .main-footer__copyright {
    display: none;
  }

  .main-footer__legal-data {
    padding: 2.7vw 3.05vw 3.3vw 3.6vw;
    font-size: 1.7vw;
    line-height: 3.1vw;
  }

  .main-footer__copyright+.main-footer__link {
    margin-left: 0;
  }
}

@media (max-width: $mobile-width) {
  .main-footer__links {
    border-bottom: none;
  }

  .main-footer__wrapper-social {
    padding: 13.8vw 4.7vw 15vw;
    justify-content: center;
  }

  .main-footer .main-footer__wrapper-social img {
    width: 67.5vw;
    height: 16.25vw;
    margin-left: 0;
  }

  .main-footer__text-hidden {
    width: 100%;
    font-size: 4.1vw;
    line-height: 7.5vw;
    padding-top: 8.5vw;
    padding-left: 1.2vw;
  }

  .main-footer .main-footer__text-hidden p {
    text-align: center;
  }

  .main-footer__social {
    padding-left: 5.3vw;
    padding-top: 9.5vw;
  }

  .main-footer__main {
    padding: 0;
  }

  .main-footer__menu,
  .main-footer__office {
    width: 100%;
    border-bottom: 1px solid $border-footer;
  }

  .main-footer h3 {
    font-size: 4.7vw;
    line-height: 5vw;
    padding-top: 9.1vw;
    padding-bottom: 8.1vw;
    padding-left: 4.7vw;
    cursor: pointer;

    &:hover,
    &:focus {
      color: $blue-color;
    }

    &:active {
      color: darken($blue-color, 10%);
    }
  }

  .main-footer .main-footer__menu h3,
  .main-footer .main-footer__office h3 {
    position: relative;

    &::before {
      position: absolute;
      width: 5vw;
      height: 0.3vw;
      background-color: $footer-plus;
      content: "";
    }

    &::after {
      position: absolute;
      width: 5vw;
      height: 0.3vw;
      background-color: $footer-plus;
      transform: rotate(90deg);
      content: "";
    }

    &::before,
    &::after {
      top: 11.25vw;
      right: 5.3vw;
    }

    &:hover::before,
    &:focus::before,
    &:hover::after,
    &:focus::after {
      background-color: $blue-color;
    }

    &:active::before,
    &:active::after {
      background-color: darken($blue-color, 10%);
    }
  }

  .main-footer .main-footer__menu--open h3,
  .main-footer .main-footer__office--open h3 {
    &::after {
      display: none;
    }
  }

  .main-footer .main-footer__office p,
  .main-footer__item {
    font-size: 4.7vw;
    line-height: 8vw;
  }

  .main-footer__navigation,
  .main-footer__wrapper-contacts {
    display: none;
    padding: 2.2vw 5.7vw 7.8vw 4.7vw;
  }

  .main-footer__menu--open .main-footer__navigation,
  .main-footer__office--open .main-footer__wrapper-contacts {
    display: block;
  }

  .main-footer__office {
    padding-left: 0;
  }

  .main-footer__legal-data {
    padding: 9vw 4.17vw 8vw;
    font-size: 4.1vw;
    line-height: 7.5vw;
  }

  .main-footer__copyright + .main-footer__link,
  .main-footer .main-footer__legal-data p {
    margin: 0 auto;
  }

  .main-footer .main-footer__legal-data p {
    margin-top: 6.56vw;
  }
}

